@use 'variables';
@use 'global';

header.primary {
  display: flex;
  justify-content: space-between;
}

header svg {
  width: 42px;
}

.user img {
  width: 30px;
  height: 30px;
  object-fit: cover;
  border-radius: 50%;
}

.header-right {
  display: flex;
  align-items: center;
  gap: 2em;
}

header.primary,
.user {
  display: flex;
  align-items: center;
  gap: 1em;
}

a.logo img {
  width: 170px;
}

// Mobile Nav
aside {
  position: fixed;
  z-index: 999;
  right: 0;
  top: 0;
  height: 100vh;
  background-color: var(--primary-color);
  width: 40%;
  padding: 3em;
  text-align: right;
}
